.scene {
    @include pageContainer();
    margin-top: 60px;
    height: 100vh;
    padding: 0;
    @include laPhone(){
        margin-top: 40px;
        height: calc(100% - 40px);
    }
    iframe {
        border-width: 0;
    }
}

