.modalScreen {
 width: 100%;
 height: 100%;
 margin: 0;
 padding: 0;
 position: fixed;
 top: 0;
 left: 0;
 background-color: $primaryColorLight;
 pointer-events: none;
 @include laTablet() {
     display: none;
 }
}

.modalScreen img {
    position: fixed;
    top: calc(50% + 38px);
    left: calc(50% - 22px);
    transform: translate(-50%, -50%);
    z-index: 500;
}

.logosSM img {
    width: 120px;
    margin: 10px 0;
    @include laPhone(){
        width: 100px;
        margin: 0;
    }
}